.rsschool-logo {
  display: block;
  width: var(--rss-logo-size);

  .st0, .st1, .st2, .st3, .st4, .st5, .st6, .st8, .st9 {
    fill: var(--text-color-2);
    stroke: var(--text-color-3);
  }

  .st1 {
    fill: var(--text-color-3);
  }
    
  .st5 {
    fill: var(--bg-color);
    stroke-width: 6;
    stroke: var(--primary-color-1);
  }

  .st7 {
    fill: var(--bg-color);
    stroke-width: 6;
    stroke: var(--primary-color-1);
  }
  
  .logo-path {
    fill: var(--bg-color);
    stroke: var(--primary-color-1);
    stroke-width: 6;
    transition: 0.1s ease-in;
  }

  .st1 {
    clip-path: url(#SVGID_2_);
  }

  .st2 {
    clip-path: url(#SVGID_4_);
  }

  .st3 {
    clip-path: url(#SVGID_6_);
  }

  .st4 {
    clip-path: url(#SVGID_8_);
  }
  
  
  .st6 {
    clip-path: url(#SVGID_8_);
  }
  
  .st6, .st7 {
    fill: none;
    stroke-width: 4;
    stroke-miterlimit: 10;
  }
  
  .st8, .st9 {
    clip-path: url(#SVGID_10_);
  }
  
  .st9 {
    fill: none;
    stroke-width: 4;
    stroke-miterlimit: 10;
  }
}
